import React from 'react';
import {
  Button, ContainedList, ContainedListItem, usePrefix
} from '@carbon/react';
import { Add } from '@carbon/react/icons';
import { useLoaderData, useNavigate } from 'react-router-dom';

import backupData from '../data/backup_data.json';
import DateTime from '../lib/date_time';

export async function loader(){
  return { books: backupData.books };
}

export default function Books(){
  const prefix = usePrefix();
  const { books } = useLoaderData();
  const navigate = useNavigate();

  return (
    <div id="books" className="books">
      <ContainedList label="Books" kind="disclosed">
        {books.map((book) => (
          <ContainedListItem key={book.id} className={`book-${book.id}`} onClick={() => navigate(`/books/${book.id}`)}>
            <div>
              <div className="name">{book.name}</div>
              <div className={`${prefix}--label ${prefix}--label--no-margin description`}>
                {book.createdAt === book.updatedAt
                  ? `Created on ${DateTime.formatDateTime(book.createdAt)}`
                  : `Updated on ${DateTime.formatDateTime(book.updatedAt)}`}
              </div>
            </div>
          </ContainedListItem>
        ))}

        <Button className="add-button" size="md" renderIcon={Add} iconDescription="Add Book" onClick={() => navigate('/books/new')}>Add Book</Button>
      </ContainedList>
    </div>
  );
}
