import React from 'react';
import { createHashRouter, RouterProvider } from 'react-router-dom';

import Books, { loader as booksLoader } from './pages/books';
import ErrorPage from './pages/error_page';
import Expenses, { loader as expenseLoader } from './pages/expenses';
import BookForm, { loader as bookFormLoader } from './pages/book_form';

const router = createHashRouter([
  {
    path:         '/',
    element:      <Books />,
    errorElement: <ErrorPage />,
    loader:       booksLoader,
  },
  {
    path:         'books',
    element:      <Books />,
    errorElement: <ErrorPage />,
    loader:       booksLoader,
  },
  {
    path:         'books/new',
    element:      <BookForm />,
    errorElement: <ErrorPage />,
  },
  {
    path:         'books/:id/edit',
    element:      <BookForm />,
    errorElement: <ErrorPage />,
    loader:       bookFormLoader,
  },
  {
    path:         'books/:id',
    element:      <Expenses />,
    errorElement: <ErrorPage />,
    loader:       expenseLoader,
  }
]);

function App(){
  return (
    <div className="App">
      {/* <header></header> */}
      <main>
        <RouterProvider router={router} />
      </main>
      {/* <footer></footer> */}
    </div>
  );
}

export default App;
