import React, { useMemo, useState } from 'react';
import {
  Button,
  Form, NumberInput, Select, SelectItem, Stack, TextInput
} from '@carbon/react';
import { useLoaderData } from 'react-router-dom';

import CONSTANTS from '../lib/constants';
import isEmpty from '../lib/is_empty';
import backupData from '../data/backup_data.json';

const bookTypes = Object.keys(CONSTANTS.bookTypes);

function getBook(id){
  return backupData.books.find((book) => book.id === id);
}

export async function loader({ params }){
  const book = await getBook(params.id);
  return { book };
}

export default function BookForm(){
  const data = useLoaderData();
  const { book = {} } = data || {};
  const [bookName, setBookName] = useState(book.name || '');
  const [bookType, setBookType] = useState(book.type || '');
  const [creditLimit, setCreditLimit] = useState(book.creditLimit || 1000);

  const disabled = useMemo(() => (
    isEmpty(bookName) || isEmpty(bookType) || (bookType === 'Credit Card' ? isEmpty(creditLimit) : false)
  ), [bookName, bookType, creditLimit]);

  return (
    <Form className="book-form" aria-label="book form">
      <Stack gap={7}>
        <TextInput
          id="book-name"
          className="book-name"
          labelText="Book Name"
          placeholder="Required"
          value={bookName}
          onChange={(e) => setBookName(e.target.value)}
          required
        />

        <Select
          id="book-type"
          className="book-type"
          // defaultValue={bookTypes[0]}
          labelText="Book Type"
          value={bookType}
          onChange={(e) => setBookType(e.target.value)}
        >
          {bookTypes.map((bt) => (
            <SelectItem key={bt} value={bt} text={bt} />
          ))}
        </Select>

        {bookType === 'Credit Card' && (
          <NumberInput
            id="credit-limit"
            className="credit-limit"
            label="Credit Limit"
            min={100}
            // max={100}
            value={creditLimit}
            step={1}
            iconDescription="Add/decrement credit limit"
            onChange={(e) => setCreditLimit(e.target.value)}
          />
        )}

        <Button className="create-book" size="md" onClick={() => {}} disabled={disabled}>Create Book</Button>
      </Stack>
    </Form>
  );
}
