import React, { useMemo } from 'react';
// import PropTypes from 'prop-types';
import { useLoaderData } from 'react-router-dom';
import {
  Button, ContainedList, ContainedListItem, Heading, Section, usePrefix
} from '@carbon/react';
import { Add, Subtract } from '@carbon/react/icons';
import { groupBy } from 'ramda';

import CONSTANTS from '../lib/constants';
import DateTime from '../lib/date_time';
import backupData from '../data/backup_data.json';

function getBook(id){
  return backupData.books.find((book) => book.id === id);
}

export async function loader({ params }){
  const book = await getBook(params.id);
  return { book };
}

export default function Expenses(){
  const prefix = usePrefix();
  const { book } = useLoaderData();
  const { expenses } = book;

  const items = useMemo(() => {
    const groupedExpenses = groupBy(
      (expense) => DateTime.formatDate(expense.transactionAt, { showYear: true }),
      expenses
    );

    const dates = Object.keys(groupedExpenses);

    return dates.map((date) => ({
      title: date,
      data:  groupedExpenses[date]
    }));
  }, [expenses]);

  return (
    <div id="expenses" className="expenses">
      <Section as="article">
        <Heading>Expenses</Heading>
      </Section>

      {items.map(({ title, data }) => (
        <ContainedList key={title} kind="disclosed" label={title}>
          {data.map((expense) => (
            <ContainedListItem key={expense.id} className={`expense expense-${expense.id}`}>
              <div>
                <div className="name">
                  <span>{expense.title}</span>
                  <span>{expense.amount}</span>
                </div>
                {/* <div className="name">{expense.amount}</div> */}
                <div className={`${prefix}--label ${prefix}--label--no-margin description`}>
                  {`at ${DateTime.formatTime(expense.transactionAt)}`}
                </div>
              </div>
            </ContainedListItem>
          ))}
        </ContainedList>
      ))}

      <div className="action-buttons">
        <Button size="md" renderIcon={Add} iconDescription="Add Expense">{`${CONSTANTS.bookTypes[book.type]} In`}</Button>
        <Button kind="danger" size="md" renderIcon={Subtract} iconDescription="Delete Expense">{`${CONSTANTS.bookTypes[book.type]} Out`}</Button>
      </div>
    </div>
  );
}

Expenses.propTypes = {
};

Expenses.defaultProps = {
};
