export default function(val){
  if (val === null){
    return true;
  }

  switch (typeof val){
    case 'number':
      return isNaN(val) || val === 0;
    case 'boolean':
      return val === false;
    case 'undefined':
      return true;
    case 'string':
      return val.trim() === '';
    default:
      return !val;
  }
}
